"use client";

import React, { useState, useEffect } from 'react';
import { useToast } from "@/hooks/use-toast";
import { openDB } from 'idb';
import { Button } from '@/components/ui/button';
import { Trash } from 'lucide-react';

interface ScreenshotViewerProps {
  id: string;
}

const getScreenshotFromIDB = async (id: string) => {
  try {
    const db = await openDB('cameraApp', 1);
    
    // Check if the object store exists before fetching
    if (!db.objectStoreNames.contains('screenshots')) {
      console.error("No 'screenshots' object store found.");
      return null;
    }

    // Attempt to get the screenshot by ID
    const imageData = await db.get('screenshots', id);
    
    // If no data is found, log a message
    if (!imageData) {
      console.warn(`No screenshot found for ID: ${id}`);
    }

    return imageData;
  } catch (error) {
    console.error("Error fetching screenshot from IDB:", error);
    return null;
  }
};

const deleteScreenshotFromIDB = async (id: string) => {
  try {
    const db = await openDB('cameraApp', 1);
    await db.delete('screenshots', id);
  } catch (error) {
    console.error("Error deleting screenshot:", error);
  }
};

export function ScreenshotViewer({ id }: ScreenshotViewerProps) {
  const { toast } = useToast();
  const [screenshot, setScreenshot] = useState<string | null>(null);

  useEffect(() => {
    // Fetch screenshot when the component mounts
    const fetchScreenshot = async () => {
      if (!id) {
        toast({ title: "Invalid ID", description: "No valid ID provided.", variant: "destructive" });
        return;
      }
      try {
        const savedScreenshot = await getScreenshotFromIDB(id);
        if (savedScreenshot) {
          setScreenshot(savedScreenshot);
        } else {
          setScreenshot(null);
        }
      } catch (error) {
        toast({ title: "Error Fetching Screenshot", description: "Could not fetch screenshot from IndexedDB.", variant: "destructive" });
        console.error("Error fetching screenshot:", error);
      }
    };

    fetchScreenshot();
  }, [id, toast]);

  const handleDeleteScreenshot = async () => {
    try {
      await deleteScreenshotFromIDB(id);
      setScreenshot(null); // Clear the state after deletion
      toast({ title: "Screenshot Deleted", description: "The screenshot has been deleted.", variant: "success" });
    } catch (error) {
      toast({ title: "Error Deleting Screenshot", description: "Could not delete screenshot from IndexedDB.", variant: "destructive" });
      console.error("Error deleting screenshot:", error);
    }
  };

  return (
    <div className="max-w-md mx-auto p-4 bg-white rounded-lg shadow-md">
      <h2 className="text-xl font-semibold mb-4">Screenshot Viewer</h2>
      {screenshot ? (
        <div className="relative">
          <img src={screenshot} alt="Captured Screenshot" className="w-full h-auto rounded-lg" />
          <Button
            variant="destructive"
            size="icon"
            onClick={handleDeleteScreenshot}
            aria-label="Delete Screenshot"
            className="absolute top-2 right-2 bg-red-600 hover:bg-red-700 text-white"
          >
            <Trash className="h-5 w-5" />
          </Button>
        </div>
      ) : (
        <p className="text-center text-gray-500">No screenshot available. Please capture one first.</p>
      )}
    </div>
  );
}
