"use client";

import React, { useState, useMemo } from 'react';
import { Button } from '@/components/ui/button';
import { CameraFeed } from './CameraFeed'; 
const url:string = "/video.mp4"
const ALL_CAMERAS_DATA = [
  { id: 'cam1', name: 'Front View', posterUrl: url, dataAiHint: 'front view' },
  { id: 'cam2', name: 'Left Side View', posterUrl: url, dataAiHint: 'left side view' },
  { id: 'cam3', name: 'Right Side View', posterUrl: url, dataAiHint: 'right side view' },
  { id: 'cam4', name: 'Top View', posterUrl: url, dataAiHint: 'top view' },
];

export function VideoPlayer() {
  const [selectedCameraIds, setSelectedCameraIds] = useState<string[]>(ALL_CAMERAS_DATA.map(c => c.id));
  const [mainCameraId, setMainCameraId] = useState<string>('cam1'); // Default main camera

  const handleToggleCamera = (cameraId: string) => {
    setSelectedCameraIds(prev =>
      prev.includes(cameraId) ? prev.filter(id => id !== cameraId) : [...prev, cameraId]
    );
  };

  const handleToggleAllCameras = () => {
    if (selectedCameraIds.length === ALL_CAMERAS_DATA.length) {
      setSelectedCameraIds([]);
    } else {
      setSelectedCameraIds(ALL_CAMERAS_DATA.map(c => c.id));
    }
  };

  const displayedCameras = useMemo(() => {
    return ALL_CAMERAS_DATA.filter(cam => selectedCameraIds.includes(cam.id));
  }, [selectedCameraIds]);

  return (
    <div className="grid grid-cols-5 grid-rows-7 gap-4 p-32 ">
      {/* Main Camera Feed */}
      <div className="col-span-4 row-span-5">
        {(
          <CameraFeed
            id={mainCameraId}
            name={displayedCameras.find(cam => cam.id === mainCameraId)?.name || ''}
            posterUrl={displayedCameras.find(cam => cam.id === mainCameraId)?.posterUrl || ''}
            dataAiHint={displayedCameras.find(cam => cam.id === mainCameraId)?.dataAiHint}
          />
        )}
      </div>

      {/* Camera 2 Feed */}
      <div className="row-span-2 col-start-1 row-start-6 col-span-1">
        {(
          <CameraFeed
            id={displayedCameras[1].id}
            name={displayedCameras[1].name}
            posterUrl={displayedCameras[1].posterUrl}
            dataAiHint={displayedCameras[1].dataAiHint}
          />
        )}
      </div>

      {/* Camera 3 Feed */}
      <div className="row-span-2 col-start-2 row-start-6 col-span-1">
        {(
          <CameraFeed
            id={displayedCameras[2].id}
            name={displayedCameras[2].name}
            posterUrl={displayedCameras[2].posterUrl}
            dataAiHint={displayedCameras[2].dataAiHint}
          />
        )}
      </div>

      {/* Camera 4 Feed */}
      <div className="row-span-2 col-start-3 row-start-6">
        { (
          <CameraFeed
            id={displayedCameras[3].id}
            name={displayedCameras[3].name}
            posterUrl={displayedCameras[3].posterUrl}
            dataAiHint={displayedCameras[3].dataAiHint}
          />
        )}
      </div>
      <div className="row-span-2 col-start-4 row-start-6">
        { (
          <CameraFeed
            id={displayedCameras[3].id}
            name={displayedCameras[3].name}
            posterUrl={displayedCameras[3].posterUrl}
            dataAiHint={displayedCameras[3].dataAiHint}
          />
        )}
      </div>

      {/* Logo */}
      <div className="col-start-5 row-start-1 flex items-center justify-center bg-white border border-gray-400">
        <h1 className="text-xl font-bold">Logo</h1>
      </div>

      {/* Controls */}
      <div className="col-start-5 row-start-2 flex items-center justify-center bg-white border border-gray-400">
        <p>Controls</p>
      </div>

      {/* Main Camera Selector */}
      <div className="row-span-5 col-start-5 row-start-3 flex flex-col items-center justify-center bg-white border border-gray-400">
        <h2 className="text-lg font-medium">Select Main Camera</h2>
        {displayedCameras.map(camera => (
          <Button
            key={camera.id}
            variant={mainCameraId === camera.id ? 'secondary' : 'outline'}
            size="sm"
            onClick={() => setMainCameraId(camera.id)}
            className="mt-2"
          >
            {camera.name}
          </Button>
        ))}
      </div>
    </div>
  );
}
